.body {
    background: #2E4158;
    height: 100%;
}

.paper {
    height: 100%;
    padding-top: 10%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.form {
    width: 100%;
    padding-top: 4%;
}

.submit {
    margin-bottom: 10%;
    margin-top: 5%;
    background-color: #52C4B9;
    color: white;
    border: none;
    outline: none;
    border-radius: 25px;
    padding: 15px 70px;
    /*font-size: .8em;*/
    /*font-weight: 500;*/
}

.submit:hover {
    background: #31a897;
}

.TextField {
    outline-color: #5ED0C0;
    color: white;
}

.MuiOutlinedInput-input {
    color: white;
}

.MuiFormLabel-root {
    color: white;
}

.MuiFormLabel-root.Mui-focused {
    color: #5ED0C0;
}

.MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline {
    border-color: #5ED0C0;
}

.MuiSvgIcon-root {
    color: white;
}

.MuiCheckbox-colorPrimary.Mui-checked {
    color: #5ED0C0;
}

/*.MuiPickersCalendarHeader-iconButton {*/
/*    color: black;*/
/*}*/

.MuiPickersDay-daySelected {
    background-color: #5ED0C0;
}

.MuiPickersDay-daySelected:hover {
    background: #31a897;
}

/*.MuiIconButton-label {*/
/*    color: white;*/
/*}*/

.MuiInput-underline:after {
    border: 1.5px solid #5ED0C0;
}

.MuiIconButton-root {
    color: black;
}

